import * as React from 'react';
import { Divider, Typography } from 'antd';
import Banner from "../full_banner.png";
const { Title, Paragraph, Text, Link } = Typography;

export interface IntroViewProps {
    contentWidth: number,
    contentHeight: number
}

export interface IntroViewState {}

export default class IntroView extends React.Component<IntroViewProps, IntroViewState> {
    constructor(props: IntroViewProps) {
        super(props);
        this.state = {
            
        };
    }
    public render() {
        let {contentWidth} = this.props;
        let breakpoint = 540;
        let img_style1 = {"height": "120px"};
        let img_style2 = {"width": "100%"};
        let used_style:any = img_style1;
        if(contentWidth > breakpoint){
            used_style = img_style1;
        }else{
            used_style = img_style2;
        }
        return (

            <div className="IntroViewBox">
                <Typography>
                <div style={{"textAlign": "center"}}>
                <img src={Banner} style={used_style} alt="banner"/>
                
                </div>
                {/*<Title level={3}>HAPI: A Large-scale Longitudinal Dataset of Commercial ML API Predictions</Title>*/}
                <Paragraph>
                Commercial ML APIs offered by providers such as Google, Amazon and Microsoft have dramatically simplified ML adoption in many applications. Numerous companies and academics pay to use ML APIs for tasks such as object detection, OCR and sentiment analysis. Different ML APIs tackling the same task can have very heterogeneous performance. Moreover, the ML models underlying the APIs also evolve over time. As ML APIs rapidly become a valuable marketplace and a widespread way to consume machine learning, it is critical to systematically study and compare different APIs with each other and to characterize how APIs change over time. However, this topic is currently underexplored due to the lack of data. Here, we present HAPI (History of APIs), a longitudinal dataset of 1,761,417 instances of commercial ML API applications (involving APIs from Amazon, Google, IBM, Microsoft and other providers) across diverse tasks including image tagging, speech recognition and text mining from 2020 to 2022. Each instance consists of a query input for an API (e.g., an image or text) along with the APIs output prediction/annotation and confidence scores. HAPI is the first large-scale dataset of ML API usages and is a unique resource for studying ML-as-a-service (MLaaS). As examples of the types of analyses that HAPI enables, we show that ML APIs performance change substantially over timeseveral APIs accuracies dropped on specific benchmark datasets. Even when the APIs aggregate performance stays steady, its error modes can shift across different subtypes of data between 2020 and 2022. Such changes can substantially impact the entire analytics pipelines that use some ML API as a component. We further use HAPI to study commercial APIs performance disparities across demographic subgroups over time. HAPI can stimulate more research in the growing field of MLaaS.
                </Paragraph>

            </Typography>
                   
            </div>
            
            
        );
    }
}
